  line-height: 36px;
  color: #000000;
}
.main__title--form {
  width: 280px;
  margin-left: auto;
  margin-right: auto;
}
.main__title--catalog {
  padding-bottom: 40px;
}
.main__subtitle {
  display: inline-block;
  margin: 0;
  margin-left: -22px;
  padding: 25px 22px 38px 22px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  background-color: #ffffff;
}
.main__subtitle--additional {
  padding-bottom: 10px;
}
.main__subtitle--owner-data {
