  .main__title {
    font-size: 60px;
    line-height: 60px;
    padding-top: 37px;
    padding-bottom: 28px;
  }
  .main__title--form {
    width: 630px;
  }
  .main__subtitle {
    margin-left: -40px;
    font-size: 36px;
    line-height: 36px;
    padding: 0 40px;
    padding-top: 40px;
  }
  .main__subtitle--catalog {
    padding: 0 37px;
    padding-top: 40px;
    padding-bottom: 80px;
    margin-left: 0;
  }
