.main-nav {
  position: absolute;
  width: 320px;
  top: 66px;
  right: -20px;
  z-index: 1;
  background-color: #ffffff;
}
.main-nav__toggle {
  position: absolute;
  padding: 0;
  top: -44px;
  right: 20px;
  width: 24px;
  height: 18px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:hover::before {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs.main-nav {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    position: static;
    margin-left: auto;
    align-items: center;
    width: auto;
    background-color: transparent;
  }
  .main-nav__toggle {
    display: none;
  }
}
.nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}
.nav-list__item {
  width: 100%;
  padding: 22px 0 24px 0;
  border-top: 1px solid #e6e6e6;
  text-align: center;
}
.nav-list__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
.nav-list__item:hover {
  opacity: 0.6;
}
.nav-list__item a {
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .nav-list {
    flex-direction: row;
    gap: 35px;
    margin-top: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .nav-list__item {
    width: auto;
    padding: 0;
    border: none;
    text-align: inherit;
  }
  .nav-list__item:last-child {
    border: none;
  }
  .nav-list__item--current {
    position: relative;
  }
  .nav-list__item--current::after {
    position: absolute;
    content: "";
    bottom: -9px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .nav-list {
    font-size: 20px;
    gap: 36px;
  }
  .nav-list__item a {
    color: #ffffff;
  }
  .nav-list__item--inside a {
    color: #000000;
  }
  .nav-list__item--current::after {
    bottom: -12px;
    background-color: #ffffff;
  }
  .nav-list__item--inside.nav-list__item--current::after {
    background-color: #68b738;
  }
}
